#center {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#b {
  width: 100vw;
  height: 100vh;
  animation: colorchange 50s infinite alternate;
  -webkit-animation: colorchange 50s infinite alternate;
}

@keyframes colorchange {
  0% {
    background: red;
  }

  25% {
    background: yellow;
  }

  50% {
    background: blue;
  }

  75% {
    background: green;
  }

  100% {
    background: red;
  }
}

@-webkit-keyframes colorchange {
  0% {
    background: red;
  }

  25% {
    background: yellow;
  }

  50% {
    background: blue;
  }

  75% {
    background: green;
  }

  100% {
    background: red;
  }
}


h1 {
  font-family: futura;

  width: 100%;

  margin: 0 0;
  text-align: center;

  color: #313131;
  font-size: 45px;
  font-weight: bold;
  animation: colorchange2 20s infinite alternate;
  -webkit-animation: colorchange2 20s infinite alternate;
